<template>
  <div class="cont">
    <Mheader :show=true>
      <div slot="title"></div>
    </Mheader>
    <div class="box">
      <div class="register">
        <img src="../../assets/images/login&register/login_03.png"/>
      </div>
      <div class="register-box">
        <mt-field placeholder="" type="tel" v-model="phone"></mt-field>

        <div class="code">
          <mt-field placeholder="" type="number" v-model="number">
          </mt-field>
          <div :class="fetchCodeMsg?'seconds':'seconding'" @click="sendCode">{{timerCodeMsg}}</div>
        </div>
        <mt-field placeholder="" type="password" v-model="password"></mt-field>
        <div class="register-btn" @click="sendRegister"></div>
        <div class=" lm-font-sm lm-margin-t-sm"> <span class="tips lm-text-red" @click="open"></span></div>
      </div>
      <Mdialog :dialog="dialog">
        <div slot="title"></div>
        <div slot="content">
          <div class="lm-margin-t"></div>
          <div>
            
          </div>
          <div class="lm-margin-t"></div>


          <div>
            1
          </div>
          <div> 2
          </div>
          <div> 3
          </div>
          <div>
            4

            
          </div>


          <div class="lm-margin-t"> 
          </div>

          <div>
            1[2012]20
          </div>

          2
          <div> 1</div>
          <div> 2</div>
          <div> 3
          </div>
          <div>
            4

            
          </div>


          <div class="lm-margin-t"> 
          </div>

          <div> 1
          </div>
          <div> 2
          </div>
          <div> 3
          </div>
          <div> 4
          </div>
          <div> a
          </div>
          <div> b
          </div>
          <div>c

            
          </div>


          <div class="lm-margin-t"> 
          </div>


          <div>
            email
          </div>

          <div class="lm-margin-t"> 
          </div>

          <div>
            
          </div>


          <div class="lm-margin-t">
            
          </div>

          <div>
            
          </div>
          <div class="lm-margin-t"> </div>


          <div> 

            
          </div>


          <div class="lm-margin-t"></div>


          <div> 
          </div>
          <div>
            
          </div>


        </div>
        <div slot="btn" @click="close"></div>
      </Mdialog>
    </div>
  </div>
</template>

<script>
  import Mheader from '../../components/Mheader'
  import Mdialog from '../../components/Mdialog'
  //import { MessageBox } from 'mint-ui';
  import {Toast} from 'mint-ui';

  export default {
    components: {
      Mheader,
      Mdialog
    },
    data() {
      return {
        phone: null,
        number: null,
        password: null,
        dialog: null,
        timerCodeMsg: '',
        fetchCodeMsg: false
      }
    },
    methods: {
      open() {
        this.dialog = true
      },
      close() {
        this.dialog = false
      },
      sendCode(){//
        if (!!!this.phone) {
          Toast('');
          return;
        }
        if (!this.isPhoneNo(this.phone)) {
          Toast('');
          return;
        }
        this.axios.post(this.url + '/api/Login/SendSMSCode', {phone: this.phone}).then((res) => {
          this.timeOut();//
        if (res.data.Code == 200) {
          Toast(res.data.Data);
        } else {
          Toast(res.data.Data);
        }
      }).catch((err) => {
          Toast('');})
      },
      isPhoneNo(phone) {  //
        var pattern = /^1[34578]\d{9}$/;
        return pattern.test(phone);
      },
      timeOut(){//
        let self = this;
        self.fetchCodeMsg = true
        let sec = 60;
        for (let i = 0; i <= 60; i++) {
          window.setTimeout(function () {
            if (sec != 0) {
              self.timerCodeMsg = sec + "s";
              sec--;
            } else {
              sec = 60;//
              self.timerCodeMsg = "";
              self.fetchCodeMsg = false
            }
          }, i * 1000)
        }
      },
      sendRegister(){//
        if (!!!this.phone) {
          Toast('');
          return;
        }
        if (!this.isPhoneNo(this.phone)) {
          Toast('');
          return;
        }
        if (!!!this.number) {
          Toast('');
          return;
        }
        if (this.number.length != 6) {
          Toast('');
          return;
        }
        if (!!!this.password) {
          Toast('');
          return;
        }
        if (this.password.length < 6 || this.password.length > 12) {
          Toast('6-12');
          return;
        }
        if (!this.verifyPassword(this.password)) {
          Toast('');
          return;
        }
        this.axios.post(this.url + '/api/Login/Register', {
          phone: this.phone,
          code: this.number,
          password: this.password
        }).then((res) =>
        {
          if (res.data.Code == 200) {
            let instance = Toast(res.data.Data);
            setTimeout(() =>
            {
              instance.close();
              this.$router.push({path: '/login'})
            }
          ,
            1000
          )
            ;

          } else {
            Toast(res.data.Data);
          }
        }
      ).
        catch((err) =>
        {
          Toast('');
        }
      )
      },
      verifyPassword(pwd){//
        let pattern = /^[A-Za-z_0-9]{6,16}$/;
        return pattern.test(pwd);
      }
    }
  }
</script>

<style scoped>
  .cont {
    height: 100vh;
    background-color: #ffffff;
  }

  .code {
    font-size: 0.55rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .code .seconds {
    height: 2.1rem;
    width: 5.5rem;
    color: #A9A9A9;
    text-align: center;
    line-height: 2.1rem;
    border: 1px solid #d9d9d9;
    border-radius: 0.2rem;
    margin-top: 0.8rem;
    pointer-events: none;
  }

  .code .seconding {
    height: 2.1rem;
    width: 5.5rem;
    color: #B4282D;
    text-align: center;
    line-height: 2.1rem;
    border: 1px solid #B4282D;
    border-radius: 0.2rem;
    margin-top: 0.8rem;
  }

  .code a {

    width: 7.5rem !important;
  }

  .box {
    font-size: 0.7rem;

  }

  .box .register-box {
    padding: 0 1.5em 1rem;
  }

  .box .register {
    height: 9rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .box .register > img {
    margin: 0 auto;
    width: 6rem;
    height: 4.0rem;
  }

  .box .register-box .register-btn {
    margin-top: 0.8rem;
    text-align: center;
    color: #fff;
    padding: 0.5rem 0;
    border-radius: 0.2rem;
    background-color: #B4282D;
  }


</style>



